<template lang="pug">
  .curso-main-container.pb-3
    BannerInterno
    .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
      .titulo-principal.color-acento-contenido
        .titulo-principal__numero
          span 1
        h1 Verificación del producto terminado

      p La calidad de un producto o servicio, está relacionada con la percepción del cliente sobre su valor. Si un cliente considera que un bien le ayuda a resolver sus necesidades, lo considerará de mayor o menor calidad en función de la manera en que percibe una serie de factores asociados a ese producto. En el caso de la panela, esta verificación se encuentra relacionada con las diferentes fases del proceso de producción, en donde como veremos, pueden presentarse inconvenientes desde el insumo, la manufactura e inclusive el almacenamiento, que lleven a presentar al consumidor un elemento incluso nocivo para la salud.      
      p A continuación, se explican algunos indicadores a tener presente en la manufactura, venta y distribución de la panela.
      
      
      .row.mt-5
        ul.lista-ul--color.lista-ul--separador
          li.d-flex
            i.fas.fa-feather
            h3 Buenas prácticas de manufactura para equipos de trefilado de panelas
      
      .row.justify-content-center.align-items-center.mb-4
        .col-lg-8
          p.mb-0 La panela, está sujeta a controles de calidad y seguridad adecuados para su manipulación, embalaje, almacenamiento, distribución, comercialización y venta. El sistema de gestión de calidad y seguridad debe prevenir los defectos y reducir las fallas naturales o inevitables hasta el punto en que no pongan en riesgo la salud de los consumidores.      
        .col-lg-4.col-md-8.col-10.order-first.order-lg-last.mb-4.mb-lg-0
          img(src='@/assets/curso/temas/tema1/img1-1.png', alt='')
      
      p.mb-4 Las normas de higiene y las normas generales de calidad que se deben mantener en el proceso de producción panelero, están relacionadas con cada operación, diagramadas en la figura 1 y en el diagrama de flujo de la figura 2.
      
      .tarjeta.slider-img1-2.p-4(data-aos="fade-down")
        SlyderA(tipo='b')
          .row.justify-content-center
            .col-lg-4.mb-4.mb-md-0
              h4 Secuencia de operaciones para la obtención de la panela
              p Todo proceso comienza con la recepción de las materias primas; las cuales, deben ser seleccionadas de acuerdo a su pureza e idoneidad para la posterior producción o procesamiento de alimentos.
            .col-lg-7
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 1.
                span Secuencia de operaciones para la obtención de panela granulada
              figure
                img.mb-3(src='@/assets/curso/temas/tema1/img1-3.svg', alt='Para la obtención de la panela granulada, se comienza con el apronte, este pasa por la molienda, luego pasa al proceso de prelimpieza, para eliminar la cachaza, eso se utiliza como alimentación animal, después de esto se pasa al proceso de clarificación y evaporación, para luego pasar al puntero y concentración, para pasar al batido y luego al tamizado y enfriamiento y por último al proceso de empaque y distribución.')
                figcaption Nota: Reproducido del Diseño del plan y documentación para la implementación de las BPM en la elaboración de la panela granulada en la planta INGAPI. Quizanga, V. (2009).
          .row.justify-content-center
            .col-lg-4.mb-4.mb-md-0
              h4 Proceso de fabricación de panelas en Colombia
              p Se denomina apronte a la serie de operaciones de corte, alce y transporte de la caña al ingenio y almacenamiento antes de que comience la molienda.
            .col-lg-7
              .titulo-sexto.color-acento-contenido.mb-2
                h5 Figura 2.
                span  Proceso de fabricación de panela en Colombia
              figure
                img.mb-3(src='@/assets/curso/temas/tema1/img1-4.svg', alt='Proceso de fabricación de panela en Colombia')
                figcaption Nota: Reproducido de Buenas prácticas agrícolas y buenas prácticas de manufactura en la producción de caña y panela. Osorio, G. (2007).
      
      
      .row.mt-5
        ul.lista-ul--color.lista-ul--separador
            li.d-flex 
