          img(src='@/assets/curso/temas/tema1/img1-5.svg', alt='')
        .col-lg-10(data-aos="fade-left")
          p Como se mencionó, la panela es un producto natural y debe ser procesada, envasada y almacenada en condiciones específicas para preservar su valor nutritivo y protegerla de insectos u hongos, de acuerdo con los requisitos y normas que rigen para su producción.
          p.mb-0 Criterios generales que se deben seguir en el proceso de elaboración de la panela:
      
      .row.justify-content-center.mb-5
        .col-lg-4.col-md-6.mb-4
