      .row.mt-5
        ul.lista-ul--color.lista-ul--separador
            li.d-flex 
              i.fas.fa-feather
              h3 Requisitos físico-químicos de la panela
      
      .row.justify-content-center.mb-5
        .col-lg-10(data-aos="fade-down")
