                  p.mb-0 Las panelas con defectos de fabricación en la misma ubicación, se pueden incorporar a nuevos procesos de fabricación, sin poner en peligro la seguridad y la calidad del producto.
      
          .titulo-sexto.color-acento-contenido.mb-3(data-aos='fade-right')
