      .row.mb-0(data-aos="fade-right")
        ul.lista-ul--color.lista-ul--separador5.mb-0
          li.d-flex.mb-0
            i.fas.fa-feather
            h3 Aditivos permitidos en la elaboración de panela
      
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-8(data-aos="fade-right")
          p De acuerdo con la Resolución 779 de 2006, que reglamenta la Ley 715 de 2001 de Higiene Tecnológica, se deben observar los parámetros en la producción y comercialización de la panela y otros materiales destinados al consumo alimentario, en este caso, la elaboración de la panela, podrán ser utilizados aditivos correctores de pH, antiespumantes agentes y blanqueadores.
          p.mb-0 Aditivos que se pueden utilizar en la producción de panela:
        .col-lg-4.col-md-8.col-7.order-sm-first.order-lg-last.mb-sm-4.d-none.d-md-block(data-aos="fade-left")
          img(src='@/assets/curso/temas/tema1/img1-10.png', alt='')
      
      .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
        .col-lg-4.col-md-6
          .tarjeta-avatar
            img(src='@/assets/curso/temas/tema1/img1-11.svg' alt='')
            .tarjeta.cfta1.w-100
              .text-center.p-4
                h4.text-center Reguladores de pH
                p Ácido fosfórico, bicarbonato de sodio, ácido cítrico (grado alimenticio), carbonato de calcio.
        .col-lg-4.col-md-6
          .tarjeta-avatar
            img(src='@/assets/curso/temas/tema1/img1-12.svg' alt='')
            .tarjeta.cfta1.w-100
              .text-center.p-4
                h4.text-center Antiespumantes
                p Aceites vegetales (grado alimenticio) y grasas.
        .col-lg-4.col-md-6
          .tarjeta-avatar
            img(src='@/assets/curso/temas/tema1/img1-13.svg' alt='')
            .tarjeta.cfta1.w-100
              .text-center.p-4
                h4.text-center Clarificantes
                p Balso, poliacrilamidas, cadillo y guácimo.
      
      .row.mt-5
        ul.lista-ul--color.lista-ul--separador
          li.d-flex 
            i.fas.fa-feather
            h3 Requisitos para la producción de panela a partir de mieles vírgenes en las centrales de acopio y procesamiento
      
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-2.col-md-4.col-6.mb-3(data-aos="fade-right")
          img(src='@/assets/curso/temas/tema1/img1-14.svg' alt='')
        .col-lg-10(data-aos="fade-left")
          p.mb-0 Para preparar una panela, a partir de miel cruda, se deben cumplir los siguientes requisitos:
      
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-5.d-none.d-lg-block(data-aos="fade-right")
          img(src='@/assets/curso/temas/tema1/img1-15.png' alt='a')
        .col-lg-7
           LineaTiempoD.color-secundario
            p.mb-0(numero="1" titulo="Centrales de acopio") Las instalaciones de recolección y procesamiento de miel deben tener un permiso de las autoridades sanitarias locales.
            p.mb-0(numero="2" titulo="Miel") La miel de mejor calidad proviene de ingenios azucareros aprobados por las autoridades sanitarias locales.
            p.mb-0(numero="3" titulo="Centrales de acopio") Los centros de acopio de miel deben garantizar la inocuidad de la miel utilizada para la producción de panelas.
            p.mb-0(numero="4" titulo="Sustancias e insumos") Sustancias y materias primas no permitidas en la producción de panelas: ditionito de sodio u otros químicos tóxicos con propiedades blanqueadoras, colorantes o sustancias tóxicas, grasas saturadas, azúcar, miel de ingenios azucareros, melaza, maíz, otros edulcorantes y las panelas devueltas, tienen un impacto en la seguridad y la calidad de la panela.
      
      .row.mt-5
        ul.lista-ul--color.lista-ul--separador
          li.d-flex 
            i.fas.fa-feather
            h3 Condiciones sanitarias de los trapiches
            
      p De acuerdo con la Resolución 779 de 2006, los ingenios azucareros deben seguir altos estándares de higiene y tecnología en sus operaciones.      
      p.mb-5 Y debe cumplir con las siguientes normas de higiene:
      
      TabsC.color-acento-contenido.mb-5(data-aos="fade-down")
        .py-4.py-md-5(titulo="Instalaciones físicas")
          .row.justify-content-center.align-items-center
            .col-md-5.col-lg-4.col-8.mb-4.mb-md-0
              img.mb-4(src='@/assets/curso/temas/tema1/img1-16.png' alt='')
            .col-lg-6
              ul.lista-ul--color.fa-lista
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Ubicadas lejos de fuentes de contaminación.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | No debe haber residuos sólidos ni suciedad alrededor.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Separadas de cualquier tipo de vivienda.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Los animales y los no usuarios no están permitidos en el área de producción.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Separación física entre las áreas de recepción, producción, almacenamiento y tratamiento de aguas residuales.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Su actividad no debe afectar la salud y el bienestar de las personas.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | No debe haber malas hierbas y objetos o materiales viejos alrededor del ingenio azucarero.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | En el ingenio azucarero, o sus alrededores, no se debe almacenar miel de molino, miel de otros ingenios paneleros, jarabe de maíz, azúcar y otros endulzantes, blanqueadores o colorantes u otras sustancias prohibidas. (Resolución 779 de 2006).
        .py-4.py-md-5(titulo="Instalaciones sanitarias")
          .row.justify-content-center.align-items-center
            .col-md-5.col-lg-4.col-8.mb-4.mb-lg-0
              img(src='@/assets/curso/temas/tema1/img1-17.png' alt='')
            .col-lg-6
              p Las fábricas de panela deben tener instalaciones sanitarias adecuadas, bien equipadas y eficientes, que estén separadas por género, bien organizadas y eficientes. (Lavamanos, duchas, inodoros).
              p Los servicios de saneamiento deben estar conectados a los sistemas de eliminación de desechos.
        .py-4.py-md-5(titulo="Personal manipulador de alimentos")
          .row.justify-content-center.align-items-center
            .col-md-5.col-lg-4.col-8.mb-4.mb-lg-0
              img(src='@/assets/curso/temas/tema1/img1-18.png' alt='')
            .col-lg-6
              ul.lista-ul--color.fa-lista
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Los operadores deben estar limpios y en buenas condiciones.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Deben lavarse las manos con agua y jabón y mantener las uñas cortas, limpias y sin esmalte.
        .py-4.py-md-5(titulo="Higiene de manos")
          .row.justify-content-center.align-items-center
            .col-md-5.col-lg-4.col-8.mb-4.mb-lg-0
              img(src='@/assets/curso/temas/tema1/img1-19.png' alt='')
            .col-lg-6
              p Las manos a menudo entran en contacto con los alimentos al manipularlos.  Por lo tanto, siempre deben mantenerse lo más higiénicas posible. Lavarse las manos antes de empezar a trabajar no es suficiente. Durante la jornada laboral, entran en contacto con lugares, alimentos y objetos que contienen bacterias dañinas y tienen un alto riesgo de infección.
              p De acuerdo con la resolución 779, todo el personal debe lavarse las manos:
              ul.lista-ul--color.fa-lista.mb-0
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Cuando empieza el trabajo.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Cada vez que cambia de operaciones durante su turno. Especialmente cuando pasa de procesar caña de azúcar a procesar el producto terminado.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Después de usar el baño.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Después de peinarse.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Después de toser, estornudar o tocarse la nariz.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Después de recoger algo del piso.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Después de manipular alimentos, desechos y desperdicios.
        .py-4.py-md-5(titulo="Estado de salud")
          .row.justify-content-center.align-items-center
            .col-md-5.col-lg-4.col-8.mb-4.mb-lg-0
              img(src='@/assets/curso/temas/tema1/img1-20.png' alt='')
            .col-lg-6
              p Los operarios, que manipulan alimentos, deben someterse a un reconocimiento médico antes de desempeñar esta función.  Asimismo, se deben realizar exámenes médicos si es necesario por razones de salud y epidemiológicas; especialmente, después de la ausencia del trabajo debido a una infección que pueda contaminar los alimentos procesados. Se deben tomar las medidas apropiadas para garantizar que los trabajadores del área de alimentos se   realicen exámenes médicos al menos una vez al año.
              p.mb-0 Los administradores del ingenio azucarero deben tomar las medidas necesarias para garantizar que ninguna persona conocida o sospechosa de tener una enfermedad transmitida por alimentos, directa o indirectamente, contamine los insumos o el producto final o tengan síntomas de alguna enfermedad o tenga presencia de llagas, erupciones cutáneas o tenga diarrea. Todo manipulador de alimentos que suponga tal riesgo, debe comunicarlo a la dirección de la empresa.
      
      
      .row.mt-5
        ul.lista-ul--color.lista-ul--separador
          li.d-flex 
            i.fas.fa-feather
            h3 Educación y capacitación
      
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-2.col-md-3.col-5.mb-3.mb-md-0(data-aos="fade-right")
          img(src='@/assets/curso/temas/tema1/img1-21.svg' alt='')
        .col-lg-10.col-md-9(data-aos="fade-left")
          p De acuerdo con la Resolución 779 de 2006, todas las personas que tengan que manipular alimentos deben recibir educación sanitaria, especialmente en el método de uso de alimentos limpios.  De igual manera, deben estar capacitados para realizar las tareas de higiene que se les asignan y evitar el deterioro de los alimentos.
          p.mb-0 La educación y la formación en el procesamiento de alimentos, involucra varias labores; estas son algunas de ellas:
          
      .row.justify-content-center.mb-5
        .col-lg-8(data-aos="fade-down")
          AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--gris1")
            .row.justify-content-center(titulo="Las empresas")
              .col-md-11
                p Deben capacitar continuamente a los trabajadores de alimentos; desde el momento en que son contratados y empoderarlos a través de cursos, programas u otros métodos efectivos de desarrollo. Esta capacitación será responsabilidad de la empresa y podrá ser realizada por la misma, los trabajadores, personas jurídicas o las autoridades sanitarias. Si el programa de capacitación es implementado por una persona natural o jurídica distinta a la misma empresa, deberá ser aprobado por la autoridad sanitaria competente. Para ello, se tomarán en cuenta los materiales didácticos, los documentos, materiales utilizados y la calificación del personal.
            .row.justify-content-center(titulo="La autoridad sanitaria")
              .col-md-11
                p Las autoridades sanitarias, ejerciendo funciones de vigilancia y control, velarán por el cumplimiento de los programas de formación en la industria alimentaria implementados por las empresas.
            .row.justify-content-center(titulo="Avisos")
              .col-md-11
                p Para garantizar el cumplimiento de las normas de higiene, se deben colocar avisos, sobre la obligación y la necesidad de cumplir con los procedimientos de la manipulación y procesamiento de alimentos, en lugares especiales.
            .row.justify-content-center(titulo="Entrenamiento al manipulador de alimentos")
              .col-md-11
                p El trabajador que manipule alimentos, debe estar capacitado para comprender y tener en cuenta los puntos de su área de responsabilidad y la importancia de su vigilancia o atención; Además, debe comprender los límites estrictos y las acciones correctivas que se deben tomar si se desvía de los límites especificados.
        .col-lg-4.d-none.d-lg-block(data-aos="fade-left")
          img(src='@/assets/curso/temas/tema1/img1-22.png' alt='')
      
      h3(data-aos="fade-right")
        i.fas.fa-leaf.cs.me-3
        |Condiciones de saneamiento
      
      p La Resolución 779 de 2006 del Ministerio de la Protección Social: establece el reglamento técnico sobre los requisitos sanitarios que se deben cumplir en la producción y comercialización de la panela para consumo humano y se dictan otras disposiciones.
      p.mb-4 Estas condiciones sanitarias son:
      
      .row.justify-content-center.mb-5
        .col-xl-4.col-lg-6.col-md-9.mb-4.mb-xl-0
          .crd_hover_txt(data-aos="fade-right")
            .crd_hover_txt--img
              img(src="@/assets/curso/temas/tema1/img1-23.png", alt="")
            .crd_hover_txt--body.w-100
              h4.mb-3.text-center Abastecimiento de agua
              ul.lista-ul.fa-lista
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | El agua utilizada debe ser potable o fácilmente esterilizable.
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Debe estar equipado con tanques de agua o fregaderos cubiertos que puedan satisfacer las necesidades de producción, de al menos un día, prevenir la contaminación, limpiar y desinfectar regularmente.
        .col-xl-4.col-lg-6.col-md-9.mb-4.mb-xl-0
          .crd_hover_txt(data-aos="fade-left")
            .crd_hover_txt--img
              img(src="@/assets/curso/temas/tema1/img1-24.png", alt="")
            .crd_hover_txt--body.w-100
              h4.mb-3.text-center Disposición de residuos sólidos
              ul.lista-ul.fa-lista
                li
                  i.fas.fa-circle.fa-xs.pt-2
                  | Los residuos sólidos deben ser retirados con la frecuencia necesaria para evitar la formación de malos olores, problemas de higiene, contaminación de los productos y el medio ambiente.
                li
